$height-link: 65px;
.header {
    position: fixed;
    width: 100%;
    z-index: 10;

    .navbar-toggler {
        color: var(--color-contrast-higher);
    }

    &:before {
        content: '';
        background-color: var(--color-bg);
        width: 100%;
        height: $height-link;
        position: absolute;
        opacity: 0.8;
    }

    .navbar {
        padding: 0 1rem;
    }

    .navbar-brand {
        position: relative;
        max-width: 200px;
        width: 100%;
        margin-right: 0;

        img {
            position: absolute;
            top: 100%;
            left: 50%;
            transform: translate(-50%, -50%);
            max-width: 150px;
        }
    }

    .nav-item {
        display: flex;
        align-items: center;

        & + .nav-item {
            border-left: 1px solid var(--color-contrast-lower);
        }

        &:hover,
        &:focus,
        &.active {
            .nav-link {
                color: var(--color-contrast-higher);

                &:before {
                    content: '';
                    position: absolute;
                    top: auto;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 4px;
                    background-color: var(--color-primary);
                }
            }
        }
    }

    .nav-link {
        position: relative;
        font-size: 16px;
        padding: 5px 0;
        overflow: hidden;

        img {
            width: 35px;
            margin-right: 0.5rem;
        }
    }

    .icon-page,
    .icon-post {
        display: block;
        left: 0;
        position: absolute;
        text-align: center;
        top: 0;
        object-position: 0 -860px;
        background-size: 65px auto;
        background-repeat: no-repeat;
        margin-left: 0.5rem;

        ~ span {
            margin-left: 43px;
        }
    }

    a {
        display: flex;
        align-items: center;
        line-height: $height-link;
        height: $height-link;
    }
}

@include media-breakpoint-down(lg) {
    .header {
        .navbar {
            background-color: var(--color-contrast-lower);

            .navbar-nav {
                margin-top: 3rem;
            }
        }

        .navbar-brand {
            max-width: 100%;
            flex: 1 1 auto;
            width: auto;
        }
    }
}
