#forum {
    @include card-bootstrap;

    .breadcrumb {
        background-color: var(--color-contrast-low);

        .breadcrumb-item {
            a {
                color: var(--color-contrast-higher);

                &:hover,
                &:focus {
                    color: var(--color-primary-dark);
                }
            }
        }
    }
}
