.home--glide {
    position: relative;

    .glide__slides {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 95vh;

        &:after {
            content: "";
            z-index: 0;
            position: absolute;
            height: 100%;
            width: 100%;
            top: auto;
            left: 0;
            right: 0;
            bottom: 0;
            opacity: 1;
            background-image: linear-gradient(0deg, var(--color-bg) 12%, transparent 100%);
        }

        .glide__slide {
            position: relative;

            &:before {
                content: "";
                z-index: 0;
                position: absolute;
                height: 100%;
                width: 100%;
                opacity: 0.5;
                background-image: linear-gradient(85deg, var(--color-bg), transparent);
            }

            img {
                max-width: 100%;
                object-fit: contain;
            }
        }

        .row {
            position: relative;
            z-index: 1;
        }

        .title {
            font-size: 3rem;
            font-weight: $font-weight-bold;
            text-shadow: 3px 3px 4px var(--color-contrast-low);
            margin-bottom: 0;
        }

        .text {
            font-size: 1.5rem;
            text-shadow: 3px 3px 4px var(--color-contrast-low);
        }
    }
}

.home--post {
    .post-preview {
        border: 0;
        border-radius: 20px;
        box-shadow: $box-shadow;

        img {
            max-height: 250px;
            object-fit: cover;
            border-top-left-radius: 17px;
            border-top-right-radius: 17px;
        }

        .card-header {
            border-top: 4px solid var(--color-primary);
            border-bottom: 1px dashed var(--color-primary);
            background-color: var(--color-contrast-low);

            h3 {
                margin-bottom: 0;
            }

            small {
                &:before {
                    content: '- ';
                }

                color: var(--color-contrast-high);
            }
        }

        .card-body {
            border: 0;
            background-color: var(--color-contrast-lower);
            border-bottom-left-radius: 17px;
            border-bottom-right-radius: 17px;
        }
    }
}

.home--info {
    iframe {
        padding: 0;
        width: 100% !important;
        box-shadow: $box-shadow;
    }

    .card-body {
        background-color: var(--color-contrast-low);
        border-radius: 17px;
        margin-bottom: 2rem;
        box-shadow: $box-shadow;
    }
}
