body {
    background-repeat: no-repeat;
}

.separator--top {
    padding-top: 10rem;
}

.background--top {
    position: relative;
    max-height: 350px;
    overflow: hidden;
    margin-bottom: -2rem;
    z-index: -1;

    &:before {
        content: "";
        z-index: 0;
        position: absolute;
        height: 100%;
        width: 100%;
        opacity: 0.5;
        background-image: linear-gradient(85deg, var(--color-bg), transparent);
    }

    &:after {
        content: "";
        z-index: 0;
        position: absolute;
        height: 100%;
        width: 100%;
        top: auto;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 1;
        background-image: linear-gradient(0deg, var(--color-bg) 12%, transparent 100%);
    }

    img {
        object-position: center;
        object-fit: cover;
    }
}

#preloader {
    z-index: 9999999;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color-bg);
    flex-direction: column;

    .spinner-border {
        margin-top: 4rem;
        height: 60px;
        width: 60px;
        border-color: var(--color-contrast-high);
        border-right-color: transparent;
    }
}
