.banner {
    position: relative;
    background-color: var(--color-contrast-low);
    margin: 0 0 1rem 0;
    padding: 9px 0;
    min-height: 66px;
    border-radius: 17px;
    overflow: hidden;
    box-shadow: $box-shadow;
    display: flex;
    align-items: center;

    &-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 66px;
        height: 100%;
        background-color: var(--color-primary);
        border-top-left-radius: 17px;
        border-bottom-left-radius: 17px;
        overflow: hidden;

        img {
            display: block;
            text-align: center;
            position: absolute;
            object-fit: cover;
            top: 50%;
            left: 50%;
            height: 66px;
            width: 66px;
            transform: translate(-50%, -50%);
            background-repeat: no-repeat;
            overflow: hidden;
        }
    }

    &-title {
        position: relative;
        display: flex;
        align-items: center;
        width: 100%;
        padding-left: 85px;
        font-size: 26px;
        font-weight: $font-weight-bold;
        text-shadow: 3px 3px 4px var(--color-contrast-low);

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 66px;
            right: auto;
            transform: translateY(-50%);
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 5px 0 5px 10px;
            border-color: transparent transparent transparent var(--color-primary);
        }
    }

    &-page,
    &-post {
        img {
            object-position: 0px -1649px;
        }
    }

    &-info {
        img {
            object-position: 0px -2166.1px;
        }
    }

    &-login {
        img {
            object-position: 0px -3012.2px;
        }
    }

    &-shop {
        img {
            object-position: 0 -2945px;
        }
    }

    &-filter {
        img {
            object-position: 0 -361px;
        }
    }

    &-cart {
        img {
            object-position: 0 -275px;
        }
    }

    &-promo {
        img {
            object-position: 0 -2818px;
        }
    }

    &-forum,
    &-wiki,
    &-vote,
    &-support,
    &-faq {
        img {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 66px;
            object-position: 0 0;
        }
    }
}
